/**
 * @author gyb(mocheer)
 * @since 2018-12-18
 */

let {
  LayerGroup,
  FeatureGroup
} = L;

/**
 * 
 */
FeatureGroup.include({
  /**
   * 
   * @param {*} map 
   */
  onAdd(map) {
    this._hideLayers = [];
    this.eachLayer(map.addLayer, map);
    this.on('layeradd', this.checkInZoomOnAdd, this)
    map.on('zoomend', this.checkByZoom, this)
  },
  /**
   * 
   * @param {*} map 
   */
  onRemove(map) {
    this._hideLayers = [];
    this.eachLayer(map.removeLayer, map);
    this.off('layeradd', this.checkInZoomOnAdd, this);
    map.off('zoomend', this.checkByZoom, this);
  },
  /**
   * @param {*} layer
   */
  checkLayerInZoom(layer) {
    let map = this._map;
    let zoom = map.getZoom();
    let {
      _hideLayers
    } = this;
    if (layer) {
      let {
        options
      } = layer;

      if ((options.minZoom && options.minZoom > zoom) || (options.maxZoom && options.maxZoom < zoom)) {
        !_hideLayers.includes(layer) && _hideLayers.push(layer);
        map.removeLayer(layer);
      } else if (_hideLayers.includes(layer)) {
        map.addLayer(layer);
      }
    }
  },
  /**
   * 
   * @param {*} e
   */
  checkInZoomOnAdd({
    layer
  }) {
    this.checkLayerInZoom(layer);
  },
  /**
   * 
   */
  checkByZoom() {
    this._hideLayers = this._hideLayers.filter(layer => this.hasLayer(layer));
    this.eachLayer(this.checkLayerInZoom, this)
  }
})


